* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.sidebar {
  min-width: 16vw;
  height: 100vh;
  padding-top: 1.4rem;
  padding-left: 1.8rem;

  background-color: #f7f7f7;
}

.icon-div {
  display: flex;
  align-items: center;
  gap: 0.6rem;
}

.avatar-img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
}

.icon-text {
  font-size: 1.1rem;
}

.sidebar-ul {
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
  padding-top: 2rem;
  padding-left: 0.6rem;
}

.sidebar-ul > div {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.sidebar-ul > div > i {
  cursor: pointer;
}

.sidebar-ul > div:last-child {
  position: relative;
}

.fa-inbox {
  color: #246fe0;
}

.fa-calendar {
  color: #058527;
}

.fa-rectangle-history {
  color: #692fc2;
}

.sidebar-li ~ .fa-plus {
  font-size: 1.1rem;
  font-weight: 400;
  position: absolute;
  right: 8px;
  transition: color 0.2s;
}

.fa-plus:hover {
  color: #692fc2;
}

.sidebar-li {
  list-style: none;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: 400;
  transition: all 0.2s;
}

.sidebar-li:hover,
.modal-buttons > button:hover {
  transform: translateY(-3px);
}

/* Project Modal */
.main-section {
  display: flex;
  width: 100vw;
  transition: all 0.2s;
}

.background-opacity {
  background: rgba(0, 0, 0, 0.35);
}

.modal-overlay,
.task-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 auto;

  /* display: none; */
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease-in, visibility 0.2s ease-out;
  z-index: -1;
}

.modal {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  background-color: #fff;
  border-radius: 0.3rem;
  min-width: 30rem;
  min-height: 12rem;
}

.modal > h3 {
  margin-bottom: 1.2rem;
  font-weight: 500;
  font-size: 1.2rem;
}

.modal > label {
  margin-bottom: 0.4rem;
  font-weight: 400;
}

.modal > input {
  padding: 0.4rem 0.8rem;
  border: 1px solid #ced4da;
  border-radius: 0.4rem;
}

.modal > input:focus {
  outline: none;
  border: 1px solid #ff3c00d2;
}

.modal-buttons {
  display: flex;
  justify-content: end;
  gap: 0.6rem;
  margin-top: 1.4rem;
}

.close-modal:hover {
  background-color: #ff3c00d2;
  color: #fff;
}

.add-project:hover,
.add-task:hover {
  background-color: #058527;
  color: #fff;
}

.close-modal,
.add-project,
.add-task {
  cursor: pointer;
  padding: 0.6rem 0.8rem;
  border: none;
  border-radius: 0.4rem;
  transition: all 0.2s;
}

.active {
  opacity: 1;
  visibility: visible;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.project-div {
  display: flex;
  margin: 1rem 0.4rem 1.2rem 3.2rem;
  transition: all 0.2s;
  padding: 0.1rem;
  border-radius: 2px;
}

.project-div > span {
  cursor: pointer;
  margin-left: 0.2rem;
  color: #131313bd;
}

.project-div:hover {
  background-color: #fff;
}

.filled-input {
  border-color: #058527 !important;
}

/* Creating task */
.task-div {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.task-modal > .modal {
  justify-content: center;
  min-width: 50vw;
  min-height: 50vh;
  row-gap: 0.8rem;
}

.task-header {
  margin: 0;
  padding: 0;
  color: #404040;
  font-size: 1.7rem;
  font-weight: 400;
}

.task-div > .fa-plus,
.task-modal--header > .fa-close {
  cursor: pointer;
  font-size: 1.2rem;
}

.task-modal--header {
  display: flex;
  justify-content: space-between;
}

.task-modal--header > h5 {
  font-size: 1.3rem;
}

.task-form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(4, 1fr);
  align-items: center;
  height: 100%;
  width: 100%;

  row-gap: 1rem;
  column-gap: 0.9rem;
}

.task-form > div {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.task-form > div > label {
  font-size: 1.05rem;
}

#task-title,
#task-date {
  padding: 0 2.4rem 1.2rem 0;
  max-height: 2.6rem;
}

#task-description {
  min-height: 9rem;
  min-width: 9rem;
}

.task-form > div:first-child {
  grid-column: 1/2;
  grid-row: 1/2;
}

.task-form > div:nth-child(2) {
  grid-column: 2;
  grid-row: 1/2;
}

.task-form > div:nth-child(3) {
  grid-column: 1/2;
  grid-row: 2/-1;
  height: 100%;
}

.task-form > div:nth-child(4) {
  grid-column: 2;
  grid-row: 2/3;
}

.task-form > div:nth-child(4) > div {
  display: flex;
  gap: 0.4rem;
}

.active-project {
  color: #000 !important;
  font-weight: 500;
}

.disable {
  pointer-events: none;
}

.task-modal--buttons {
  grid-row: 4 / -1;
  grid-column: 1 / -1;
  display: flex;
  justify-content: end;
  gap: 0.4rem;
}

/* TASKS */
.task-section,
.today-task--section {
  width: 100%;
  padding: 8rem 5rem 0 5rem;
}

.task-container {
  padding: 1.2rem;
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-around; */
  gap: 1.4rem;
}

.task-information {
  background: #692fc2;
  color: #fff;
  display: flex;
  flex-direction: column;

  width: 20rem;
  min-height: 200px;
  padding: 0.8rem 1.2rem;
  border-radius: 1rem;
  gap: 1rem;
}

.task-header--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.fa-check-to-slot,
.fa-xmark-to-slot {
  font-size: 1.2rem;
  cursor: pointer;
}
.fa-xmark-to-slot {
  color: red;
}

.fa-check-to-slot {
  color: #06b534;
}

.task-footer--container {
  display: flex;
  height: 100%;
  gap: 0.7rem;
  align-items: flex-end;
  justify-content: space-between;
}

time {
  margin-left: 0.4rem;
}

.task-information > div > .fa-calendar {
  color: #692fc2;
  background-color: #fff;
  padding: 0.4rem 0.6rem;
  border-radius: 0.4rem;
  gap: 0.4rem;
  margin-right: 0.2rem;
  font-size: 0.9rem;
}

.fa-gauge-low,
.fa-gauge,
.fa-gauge-high,
.fa-user {
  font-size: 1.3rem;
  cursor: pointer;
}

.fa-gauge-low {
  color: #06b534;
}

.fa-gauge {
  color: #efef08cc;
}

.fa-gauge-high {
  color: #e90202da;
}

.fa-user {
  font-size: 1.2rem;
}

.fa-user > span {
  margin-left: 0.4rem;
}
